/* ==========================================================================
   Elements Holder shortcode responsive style - begin
   ========================================================================== */

.edgtf-elements-holder {

    $columns_label: ('two', 'three', 'four', 'five', 'six');

    @include laptop-landscape-medium {

        &.edgtf-responsive-mode-1280 {

            @for $i from 0 to length($columns_label) {
                &.edgtf-#{nth($columns_label,$i+1)}-columns {

                    .edgtf-eh-item {
                        width: 100%;
                        height: auto;
                        display: inline-block;
                    }
                }
            }

            &.edgtf-one-column-alignment-left {

                .edgtf-eh-item {

                    .edgtf-eh-item-content {
                        text-align: left;
                    }
                }
            }

            &.edgtf-one-column-alignment-right {

                .edgtf-eh-item {

                    .edgtf-eh-item-content {
                        text-align: right;
                    }
                }
            }

            &.edgtf-one-column-alignment-center {

                .edgtf-eh-item {

                    .edgtf-eh-item-content {
                        text-align: center;
                    }
                }
            }
        }
    }

    @include ipad-landscape {

